
.eleven_content{
  background: url(../img/eleven_backgorund.png);
  background-size:cover;
  background-repeat: no-repeat;
  margin-top: 166px;
}

.contact_page{
  max-width: $max-width;
  margin: 0 auto;
  text-align: center;
}

.contact_page h2{
  padding-top: 97px;
}

.contact_page h4{
  font-weight: 900;
}

.container_to_input{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-column-gap: 20px;
  grid-row-gap: 20px;
  padding-top: 148px;
}

.container_to_input input{
    font: 15px $font-lato;
    color: $color_for_grey_h2;
    border:none;
    width: 100%;
    height: 60px;
    padding-left: 20px;
    border: 1px solid $color_for_grey_h2;
}

textarea{
  font: 15px $font-lato;
  color: $color_for_grey_h2;
  margin-top: 15px;
  padding-left: 20px;
  padding-top: 20px;
  border: none;
  width: 100%;
  height: 510px;
  padding-bottom: 25px;
  resize: none;
  border: 1px solid $color_for_grey_h2;
}

.message .form-main-button{
  text-align: left;
}

.message .form-main-button button{
  margin-top: 22px;
  width: 145px;
  font: 16px $font-article;
  font-weight: bolder;
  margin-bottom: 127px;
  background-color: $color_for_yellow;
}

.message .form-main-button button:hover{
  background-color: $color_for_h1;
  color: $color_for_yellow;
}

@media (min-width:1025px) and (max-width:1199px) {

.form_bottom{
  padding-left: 20px;
  padding-right: 20px;
}
}

@media (min-width:769px) and (max-width:1024px) {
  /*  Eleven Content  */

.eleven_content{
  margin-top: 100px;
}

.contact_page h2{
  padding-top: 60px;
}

.form_bottom{
  padding-left: 20px;
  padding-right: 20px;
}

.container_to_input{
  padding-top: 80px;
}

.message .form-main-button button{
  margin-bottom: 80px;
}
}

@media (min-width:426px) and (max-width:768px) {
  /*  Eleven Content  */

.eleven_content{
  margin-top: 35px;
}

.contact_page h2{
  padding-top: 40px;
}

.container_to_input{
  grid-template-columns: repeat(1, 1fr);
  grid-row-gap: 20px;
  padding-top: 40px;
  padding-left: 15px;
  padding-right: 15px;
}

.message{
padding-right: 15px;
padding-left: 15px;
}

.message .form-main-button button{
  margin-bottom: 65px;
}
}

@media (max-width:425px) {
  /*  Eleven Content  */

.eleven_content{
  margin-top: 35px;
}

.contact_page h2{
  padding-top: 40px;
}

.container_to_input{
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 20px;
  padding-top: 40px;
  padding-left: 12px;
  padding-right: 12px;
}

.message{
  padding-right: 12px;
  padding-left: 12px;
}

textarea{
  height: 400px;
}

.message .form-main-button button{
  width: 105px;
  font: 15px $font-article;
  font-weight: bolder;
  margin-bottom: 55px;
}
}
